<template>

  <div>
    <h3>合同模板信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col :xs="24" :sm="24" :md="24" :lg="24">
        <Form :model="formData" :label-width="100">
          <FormItem label="模板ID">
            <Input disabled v-model="formData.template_no" placeholder="请填写模板编号"></Input>
          </FormItem>
          <FormItem label="模板名称">
            <Input disabled v-model="formData.name" placeholder="请填写模板名称"></Input>
          </FormItem>
          <FormItem label="模板备注">
            <Input disabled v-model="formData.remark" placeholder="请填写模板备注"></Input>
          </FormItem>
          <FormItem label="合同详情">
            <div v-html="formData.rich_text">

            </div>
          </FormItem>
          <FormItem label="自定义变量">
            <Row v-if="formData.content" v-for="(item,index) in formData.content" v-bind:key="index"
                 style="margin-bottom: 20px">
              <Col style="text-align: center" :span="2">
                名称
              </Col>
              <Col :span="8">
                <Input disabled v-model="item.name"
                       placeholder="请输入变量名称"></Input>
              </Col>
              <Col style="text-align: center" :span="2">
                替换符
              </Col>
              <Col :span="8">
                <Input disabled v-model="item.placeholder"
                       placeholder="请输入变量替换符"></Input>
              </Col>
            </Row>

          </FormItem>

        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {
      },
      load: false
    }
  },
  mounted () {
    this.loadData()
  },
  methods: {

    loadData () {
      this.load = true
      api.detail({ id: this.$route.params.historyId }, res => {
        this.load = false
        this.formData = res.data.data
        this.formData.space_id = this.formData.space.id
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

